<template>
  <div>
    <h1>视频界面</h1>
    <div id="dplayer"></div>
  </div>
</template>

<script>
import DPlayer from 'dplayer';
export default {
  name: "player",

 mounted(){
     const dp = new DPlayer({
        container: document.getElementById('dplayer'),//视频自动播放
        autoplay:false,//视频自动播放
        preload:'none',//视频预加载，可选值: 'none', 'metadata', 'auto'
        volume:0.5,//默认音量
        playbackSpeed:[0.5, 0.75, 1, 1.25, 1.5, 2], //可选的播放速率，可以设置成自定义的数组
        video: {
            url: "https://api.dogecloud.com/player/get.mp4?vcode=5ac682e6f8231991&userId=17&ext=.mp4",
            pic:'',
        },
     });
     console.log(dp);
     //dp.seek(time: number): 跳转到特定时间
     //dp.pause(): 暂停视频
     //dp.play(): 播放视频
 }
};
</script>

<style scoped>
 #dplayer{
     max-height: 700px;
     width: auto;
 }
</style>